export default "* {\n  font-family: Helvetica, Arial, sans-serif;\n}\n\n.pokemon-list {\n  width: calc( 100% - 350px );\n  transition: width 0.5s;\n}\n\n.pokemon-drawer {\n  position: fixed;\n  right: 0;\n  top: 0;\n  width: 300px;\n  background-color: #f1f1f1;\n  height: 100%;\n  padding: 20px;\n  box-shadow: #000 0px 0px 17px -7px;\n}\n\n.drawer-closed {\n  transition: right 0.5s;\n  right: -270px;\n}\n\n.drawer-open {\n  transition: right 0.5s;\n}\n\n.drawer-closed app-pokemon-details {\n  display: none;\n}\n\n.drawer-control {\n  background-color: white;\n  border: 1px solid;\n  display: inline-block;\n  cursor: pointer;\n}\n\n.drawer-control:hover {\n  cursor: pointer;\n  background-color: #aaa;\n}\n\n.bar1, .bar2, .bar3 {\n  width: 22px;\n  height: 2px;\n  background-color: #333;\n  margin: 5px 0;\n  transition: 0.4s;\n}\n\n.change .bar1 {\n  transform: rotate(-45deg) translate(-6px, 5px);\n}\n\n.change .bar2 {\n  opacity: 0;\n}\n\n.change .bar3 {\n  transform: rotate(45deg) translate(-5px, -4px);\n}\n\n.full-width-list {\n  width: calc( 100% - 80px );\n  transition: width 0.5s;\n}\n/*# sourceMappingURL=data:application/json;base64, */"